import express from 'express';
import logout from '../controllers/logout_post';
import { baseUrl } from '../baseurl';

const router = express.Router();

/**
 * @swagger
 * /auth/logout:
 *   post:
 *     tags:
 *       - Authentication
 *     description: Returns status code - 200 if logout is successful
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: header
 *         name: Authorization
 *         schema:
 *           type: string
 *           required: true
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *              message:
 *                  type: string
 */

router.post(`${baseUrl}/logout`, logout);

export default router;
